.MatchPage {
    display: grid;
    grid-template-rows: 1fr;
    grid-row-gap: 1px;
    //list-style-type: ;

    .year-selector {
        display: flex;
        flex-direction: row;
        position: sticky;
        top: 0;
        background-color: #393e46;
        padding: 10px;
        .year{
            margin-left: 10px;
            
        }
    }

    .page-heading {
        margin: 20px auto;
    }
}